import React from "react";
import * as Semantic from "semantic-ui-react";
import PropTypes from "prop-types";
import { NavLink } from "react-router-dom";
import { useSwipeable } from "react-swipeable";

const DefaultLayout = ({ header, menuItems, children }) => {
  const [visible, setVisible] = React.useState(false);

  const renderMenu = (menuItems) => {
    if (!menuItems) {
      return;
    }

    return menuItems.map((item, i) => {
      const url = item.link;

      return (
        <Semantic.Menu.Item as={NavLink} key={i + item.name} to={url}>
          <Semantic.Icon name={item.icon} />
          {item.name}
        </Semantic.Menu.Item>
      );
    });
  };

  const swipeHandlers = useSwipeable({
    onSwipedLeft: () => setVisible(false),
    onSwipedRight: () => setVisible(true),
    preventDefaultTouchmoveEvent: true,
    trackMouse: true,
    trackTouch: true,
  });

  const renderMenuTrigger = () => {
    const icon = visible ? "chevron circle left" : "chevron circle right";
    return (
      <Semantic.Icon
        size="big"
        className="sidebar-open pulse"
        onClick={() => setVisible(true)}
        name={icon}
      />
    );
  };

  return (
    <div {...swipeHandlers}>
      <Semantic.Sidebar.Pushable
        as={Semantic.Segment}
        style={{ minHeight: "100vh" }}
      >
        <Semantic.Sidebar
          as={Semantic.Menu}
          animation="push"
          icon="labeled"
          onHide={() => setVisible(false)}
          vertical
          visible={visible}
          width="thin"
          inverted
        >
          <React.Fragment>{renderMenu(menuItems)}</React.Fragment>
        </Semantic.Sidebar>

        <Semantic.Sidebar.Pusher>
          {renderMenuTrigger()}

          <Semantic.Container style={{ minHeight: "100vh" }}>
            <Semantic.Header as="h2" icon textAlign="center">
              {header}
            </Semantic.Header>

            {children}
          </Semantic.Container>
        </Semantic.Sidebar.Pusher>
      </Semantic.Sidebar.Pushable>
    </div>
  );
};

DefaultLayout.propTypes = {
  menuItems: PropTypes.array,
  children: PropTypes.object,
};

export default DefaultLayout;
