import React from "react";
import DefaultLayout from "../layout/default-layout";
import MainRoute from "./../../route/main-route";

const Homepage = () => {
  const menuItems = [
    {
      name: "Home",
      icon: "home",
      link: "/",
    },
    {
      name: "Skill",
      icon: "content",
      link: "/skill",
    },
    {
      name: "Portfolio",
      icon: "book",
      link: "/portfolio",
    },
    {
      name: "Resume",
      icon: "download",
      link: "/files/resume.pdf",
    },
  ];

  return (
    <DefaultLayout
      header={""}
      menuItems={menuItems}
    >
      <MainRoute/>
    </DefaultLayout>
  );
};

export default Homepage;
