import React from "react";
import { Button } from "semantic-ui-react";

const error = (message) => {
  return (
    <React.Fragment>
      <h2>{message}</h2>

      <Button as="a" href="/">
        Back
      </Button>
    </React.Fragment>
  );
};

export const PageNotFound = () => {
  return error("Oops, the requested page no longer exist.");
};

export const PageUnderConstruction = () => {
  return error("Work in progress... Please check back later.");
};
