import React from "react";
import * as Semantic from "semantic-ui-react";

export const CardFeedItem = ({ image, date, children }) => {
  return (
    <Semantic.Feed>
      <Semantic.Feed.Event>
        <Semantic.Feed.Label image={image} />
        <Semantic.Feed.Content>
          <Semantic.Feed.Date content={date} />
          <Semantic.Feed.Summary>{children}</Semantic.Feed.Summary>
        </Semantic.Feed.Content>
      </Semantic.Feed.Event>
    </Semantic.Feed>
  );
};

export const SimpleCardFeedItem = ({
  title,
  description,
  image,
  content,
  children,
}) => {
  return (
    <Semantic.Card.Content>
      <CardFeedItem image={image} date={description}>
        {title}
      </CardFeedItem>

      <Semantic.Feed.Extra text>
        {content}
        {children}
      </Semantic.Feed.Extra>
    </Semantic.Card.Content>
  );
};
