import React from "react";
import * as Semantic from "semantic-ui-react";

const SocialMediaLink = ({ href, label, color, icon }) => {
  return (
    <Semantic.Button
      as="a"
      href={href}
      target="_blank"
      circular
      color={color}
      icon={icon}
    >
      {/* {label} */}
    </Semantic.Button>
  );
};

export default SocialMediaLink;
