import React from "react";
import { Route, Switch } from "react-router-dom";
import Portfolio from "./../container/view/portfolio";
import Skill from "./../container/view/skill";
import About from "./../container/view/about";
import { PageUnderConstruction, PageNotFound } from "../container/view/error";

const MainRoute = () => {
  return (
    <Route>
      <div>
        <Switch>
          <Route exact path="/" component={About} />
          <Route path="/skill" component={Skill} />
          <Route path="/portfolio" component={Portfolio} />
          <Route
            exact
            path="/files/resume.pdf"
            render={() => (window.location = "/files/resume.pdf")}
          />
          <Route path="/application/*" component={PageUnderConstruction} />
          <Route path="/under-construction" component={PageUnderConstruction} />
          <Route path="/404" component={PageNotFound} />
          <Route path="*" component={PageNotFound} />
        </Switch>
      </div>
    </Route>
  );
};

export default MainRoute;
