import React from "react";
import * as Semantic from "semantic-ui-react";

const LayoutHeaderBody = ({ header, children, background }) => {
  const backgroundStyle = {
    background: `url(${background})`,
    opacity: "0.5",
  };

  return (
    <Semantic.Grid>
      <div className="overlay" style={backgroundStyle} />
      <Semantic.Grid.Row columns={1}>
        <Semantic.Grid.Column>{header}</Semantic.Grid.Column>
      </Semantic.Grid.Row>

      <Semantic.Grid.Row columns={1}>
        <Semantic.Grid.Column>{children}</Semantic.Grid.Column>
      </Semantic.Grid.Row>
    </Semantic.Grid>
  );
};

export default LayoutHeaderBody;
