import React from "react";

import {
  VictoryChart,
  VictoryBar,
  VictoryTheme,
  VictoryPolarAxis,
  VictoryStack,
} from "victory";

import { Image } from 'semantic-ui-react';

export const BasePolarChart = ({ data, primaryColor, secondaryColor }) => {
  const getStyle = (fillColor) => {
    const n = data.length ? data.length : 1;
    return {
      data: {
        fill: fillColor,
        width: 360 / n,
        stroke: "#FFFFFF",
        strokeWidth: 1,
      },
    };
  };

  const styles = {
    primary: getStyle(primaryColor),
    secondary: getStyle(secondaryColor),
    radius: 20,
  };

  return (
    <VictoryChart
      polar
      theme={VictoryTheme.material}
      domainPadding={{ y: 10 }}
      innerRadius={styles.radius}
    >
      <VictoryPolarAxis tickValues={data.map((v) => v.name)} />

      <VictoryStack style={{ data: { width: 50 } }}>
        <VictoryBar
          style={styles.primary}
          data={data.map((v) => {
            return { x: v.name, y: v.value };
          })}
        />

        <VictoryBar
          style={styles.secondary}
          data={data.map((v) => {
            return { x: v.name, y: v.value * 0.2 };
          })}
        />
      </VictoryStack>
    </VictoryChart>
  );
};

export const BaseBarChart = ({ data, barColor, labelColor }) => {
  const getStyle = {
    data: {
      fill: barColor,
      labels: {
        fontSize: 12,
        fill: labelColor,
      },
    },
  };

  return (
    <VictoryChart theme={VictoryTheme.material} maxDomain={{ y: 10 }}>
      <VictoryBar
        barRatio={0.3}
        style={getStyle}
        alignment="start"
        data={data.map(v => {
          return { x: v.name, y: v.value };
        })}
      />
    </VictoryChart>
  );
};
