import React from "react";
import * as Semantic from "semantic-ui-react";

export const SimpleBulletList = ({ items }) => {
  return (
    <Semantic.List as="ul">
      {items.map((data, key) => (
        <Semantic.List.Item as="li" key={key}>{data}</Semantic.List.Item>
      ))}
    </Semantic.List>
  );
};
