import React from "react";
import * as Semantic from "semantic-ui-react";
import ListMacro from "../../component/list-macro";
import LazyImage from "./../../component/lazy-image";

const Portfolio = () => {
  const list = [
    {
      data: "Back End",
      children: [
        { data: "Symfony" },
        { data: "MySql" },
        { data: "MongoDB" },
        { data: "Google Cloud" },
      ],
    },
    {
      data: "Front End",
      children: [{ data: "React JS" }, { data: "Material UI" }],
    },
  ];

  return (
    <React.Fragment>
      <Semantic.Header as="h2">View My Work</Semantic.Header>

      <p>These will eventually be sample mockup of my previous work.</p>

      <p>This website was created with Symfony and React.</p>

      <Semantic.CardGroup stackable itemsPerRow={3}>
        <Semantic.Card color="red">
          <LazyImage url="https://picsum.photos/175/175" alt="Not Found" />
          <Semantic.CardContent>
            <Semantic.Card.Header>Shop Titan</Semantic.Card.Header>

            <Semantic.Card.Meta>
              {/* <a href="https://stackcompletes.com/shop-titans">
                stackcompletes.com/shop-titans
              </a> */}
            </Semantic.Card.Meta>
            <Semantic.Card.Description>
              <Semantic.Container textAlign="left">
                A work-in-progress website dedicated to fans of this mobile
                game.
                <Semantic.Divider />
                The front end of this website was created using a combinatinon
                of React and Material UI. The webpage and api are handled by
                Symfony framework on the backend. Data are persisted on MySql
                and MongoDb. Hosted and deployed through Google's cloud
                platform.
                <Semantic.Divider />
                <ListMacro data={list}></ListMacro>
              </Semantic.Container>
            </Semantic.Card.Description>
          </Semantic.CardContent>
        </Semantic.Card>

        <Semantic.Card color="red">
          <LazyImage url="https://picsum.photos/175/175" alt="Not Found" />
          <Semantic.CardContent>
            <Semantic.Card.Header>Financial Application</Semantic.Card.Header>

            <Semantic.Card.Meta>
              {/* <a href="/application/finance">
                stackcompletes.com/application/form-application
              </a> */}
            </Semantic.Card.Meta>
            <Semantic.Card.Description>
              <Semantic.Container textAlign="left">
                A mock up of a financial application from my previous work.
              </Semantic.Container>
            </Semantic.Card.Description>
          </Semantic.CardContent>
        </Semantic.Card>

        <Semantic.Card color="red">
          <LazyImage url="https://picsum.photos/175/175" alt="Not Found" />
          <Semantic.CardContent>
            <Semantic.Card.Header>Marketing Application</Semantic.Card.Header>

            <Semantic.Card.Meta>
              {/* <a href="/application/marketing">stackcompletes.com/application/marketing</a> */}
            </Semantic.Card.Meta>
            <Semantic.Card.Description>
              <Semantic.Container textAlign="left">
                A sample widget that allow any user to easily customize and send
                email.
              </Semantic.Container>
            </Semantic.Card.Description>
          </Semantic.CardContent>
        </Semantic.Card>
      </Semantic.CardGroup>
    </React.Fragment>
  );
};

export default Portfolio;
