import React from "react";
import * as Semantic from "semantic-ui-react";
import LayoutHeaderBody from "../layout/layout-header-body";
import SocialMediaLink from "../../component/social-media-link";
import * as Portrait from "../../assets/portrait.jpg";
import * as beach from "../../assets/beach.jpg";

const About = () => {
  const header = (
    <Semantic.Container>
      <Semantic.Grid className="title">
        <Semantic.GridRow>
          <Semantic.GridColumn width={16}>
            <Semantic.Header as="h2" icon textAlign="center" className="header">
              <Semantic.Header.Content>
                <b>Hi, I am <span className="pop">Leonard Lam</span>.</b>
              </Semantic.Header.Content>
            </Semantic.Header>
          </Semantic.GridColumn>
        </Semantic.GridRow>

        <Semantic.GridRow>
          <Semantic.GridColumn
            width={5}
            textAlign="right"
            verticalAlign="middle"
            className="desc-list"
          >
            <div className="desc-list sm">SOLID</div>
            <div className="desc-list md">Performance</div>
            <div className="desc-list lg">Result</div>
          </Semantic.GridColumn>

          <Semantic.GridColumn width={6}>
            <Semantic.Image src={Portrait} circular size="medium" centered />
          </Semantic.GridColumn>

          <Semantic.GridColumn
            width={5}
            textAlign="left"
            verticalAlign="middle"
            className="description"
          >
            I personalize technical strategies to meet your requirements.
          </Semantic.GridColumn>
        </Semantic.GridRow>

        <Semantic.GridRow>
          <Semantic.GridColumn width={16}>
            <div className="subtitle">Software Engineer / Full Stack Developer</div>
          </Semantic.GridColumn>
        </Semantic.GridRow>

        {/* <hr className="impact"></hr> */}

        <Semantic.GridColumn width={16} textAlign="center">
          <SocialMediaLink
            href="https://github.com/leo-webstack"
            label="Github"
            color="black"
            icon="github"
          ></SocialMediaLink>
          <SocialMediaLink
            href="https://linkedin.com/in/leo-webstack"
            label="LinkedIn"
            color="linkedin"
            icon="linkedin"
          ></SocialMediaLink>
        </Semantic.GridColumn>
      </Semantic.Grid>
    </Semantic.Container>
  );

  return (
    <React.Fragment>
      <LayoutHeaderBody
        header={header}
        className="background-image"
        background={beach}
      ></LayoutHeaderBody>
    </React.Fragment>
  );
};

export default About;
