import React from "react";
import * as Semantic from "semantic-ui-react";
import * as ReactLogo from "../../assets/react.png";
import * as AngularLogo from "../../assets/angular.png";
import * as PhpLogo from "../../assets/php.png";
import * as MySqlLogo from "../../assets/mysql.png";
import * as ApacheLogo from "../../assets/apache.png";
import { BaseBarChart } from "../../component/base/base-chart";
import { SimpleCardFeedItem } from "./../../component/card-feed-item";
import { SimpleBulletList } from "../../component/list";
import { Paragraph } from "../../component/base/base-text";

const Skill = () => {
  const skillData = [
    { name: "Angular", value: 5, logo: AngularLogo },
    { name: "React", value: 5, logo: ReactLogo },
    { name: "Symfony", value: 8.5 },
    { name: "Drupal", value: 7 },
  ];

  const languages = [
    { name: "PHP", value: 8.5, logo: PhpLogo },
    { name: "Java", value: 5 },
    { name: "JavaScript", value: 5 },
    { name: "TypeScript", value: 5 },
  ];

  const other = [
    { name: "Microservices", value: 5 },
    { name: "REST", value: 7 },
    { name: "Cloud", value: 5 },
    { name: "Apache", value: 7, logo: ApacheLogo },
    { name: "MySql", value: 7, logo: MySqlLogo },
  ];

  return (
    <React.Fragment>
      <Semantic.Header as="h2" id="skill-set">
        Skill
      </Semantic.Header>

      <Semantic.Grid stackable columns={2}>
        <Semantic.Grid.Column>
          <Semantic.Card fluid>
            <Semantic.Card.Content>
              <Semantic.Card.Header>Back End</Semantic.Card.Header>
            </Semantic.Card.Content>
            <SimpleCardFeedItem
              title="PHP"
              image={PhpLogo}
              description="General purpose server sided language."
            >
              <Paragraph>
                I have been working with PHP for the past 4 years. With the
                release of 7.x and strong presence in the e-commerce space, PHP
                is solid and well tested language. It flexibility and
                application of OOP principles make it an appealing option.
              </Paragraph>
            </SimpleCardFeedItem>

            <SimpleCardFeedItem
              title="Apache/Nginx"
              image={ApacheLogo}
              description="Web servers."
              content=""
            >
              <Paragraph>
                This website is currently running on Apache. Although Nginx
                provide superior performance, the ability to fully control
                directory through the use of .htaccess and low visitor count
                make it the optimal choice for web servers.
              </Paragraph>

              <Paragraph>
                With the availablity of powerful cache storage such as Memcached
                and Redis. We can easily scale as the complexity of the business
                logic and number of request increases.
              </Paragraph>
            </SimpleCardFeedItem>
            <SimpleCardFeedItem
              title="MySql/PostgreSql/NoSql"
              image={MySqlLogo}
              description="Data storage."
            >
              <Paragraph>
                My previous hands on experience with databases mostly involved data
                modelling. There were several considerations when my team selected a
                database.
              </Paragraph>

              <SimpleBulletList
                items={[
                  "Memory Usage",
                  "ER Model",
                  "Documentation & Community Support",
                ]}
              />

              <Paragraph>
                Although PostgreSql provides newer features and better data
                integrity. I don't think its primary benefit outweighed our memory usage
                requirement. Memory is costly and we can easily swap MySql
                with PostgreSql in the future if necessary.
              </Paragraph>

              <Paragraph>
                NoSql provides high efficiency when dealing with massive amount of
                unstructured data. There are instances in the future where it is beneificial.For the
                purpose of our system, our data needed to be tightly coupled and
                managed on the application layer.
              </Paragraph>
            </SimpleCardFeedItem>
          </Semantic.Card>
        </Semantic.Grid.Column>

        <Semantic.Grid.Column>
          <Semantic.Card fluid>
            <Semantic.Card.Content>
              <Semantic.Card.Header>Front End</Semantic.Card.Header>
            </Semantic.Card.Content>
            <SimpleCardFeedItem
              title="React"
              image={ReactLogo}
              description="User interface."
              content=""
            >
              <Paragraph>
                React is currently the most popular JS library for manipulating
                the user interface. It has minimally opinionated design which
                enables flexible usage in many web application. A simple SPA
                project such as this website is well suited. My previous work
                involved a mixture of jQuery, React and Angular depending on the
                product we were building.
              </Paragraph>
            </SimpleCardFeedItem>
            <SimpleCardFeedItem
              title="Angular"
              image={AngularLogo}
              description="JS Framework - MVVM Architecture"
            >
              <Paragraph>
                Although Angular is frequently compared to React, it
                differentiate itself as JS framework. It provides many feature
                out of the box. Its primary benefit lies in code security and
                consistency which is the reason we seleceted it for certain web
                page. The built in libraries and well maintained code base allow
                future developer to easily dive into the code and understand its
                structure.
              </Paragraph>
            </SimpleCardFeedItem>
          </Semantic.Card>
        </Semantic.Grid.Column>
      </Semantic.Grid>

      <Semantic.Grid stackable columns={2}>
        <Semantic.GridColumn>
          <BaseBarChart data={skillData} labelColor="#333" barColor="coral" />
        </Semantic.GridColumn>

        <Semantic.GridColumn>
          <BaseBarChart
            data={languages}
            labelColor="#333"
            barColor="lightblue"
          />
        </Semantic.GridColumn>

        <Semantic.GridColumn>
          <BaseBarChart data={other} labelColor="#333" barColor="lightgreen" />
        </Semantic.GridColumn>
      </Semantic.Grid>
    </React.Fragment>
  );
};

export default Skill;
