import React from "react";
import * as Semantic from "semantic-ui-react";

const ListMacro = ({ data }) => {
  const renderChildren = (menuItems) => {
    return menuItems.map((menuItem, i) => {
      if ("children" in menuItem) {
        return renderData(menuItem);
      }

      return <Semantic.List.Item key={i}>{menuItem.data}</Semantic.List.Item>;
    });
  };

  const renderData = (menuItems) => {
    return menuItems.map((item, i) => {
      if ("children" in item) {
        return (
          <Semantic.List.Item key={i}>
            {item.data}

            <Semantic.List.List>
              {renderChildren(item.children)}
            </Semantic.List.List>
          </Semantic.List.Item>
        );
      }

      return <Semantic.List.Item>{item.data}</Semantic.List.Item>;
    });
  };

  return <Semantic.List>{renderData(data)}</Semantic.List>;
};

export default ListMacro;
